import { Toolbar, Paper, Tab, Tabs, Container } from '@mui/material'
import React from 'react'

import Home from './profile/Home'
import Friends from './profile/Friends'
import Raiting from './profile/Raiting'
import { Header } from '../components/Header'
import BottomNav from '../components/BottomNav'
import { Panel } from '@vkontakte/vkui'
import { PAGE_PROFILE } from '../routers'

function Profile({fetchedUser}) {
  const [ value, setValue ] = React.useState("one")

  const handleChange = (event, newValue) => {
    setValue(newValue);
  }

  return (
    <Paper sx={{ width: '100vw', height: '100%', minHeight: '100vh', borderRadius:0 }}>
      <Container>
      <Header fetchedUser={fetchedUser}/> 
      <Tabs
      value={value}
      onChange={handleChange}
      textColor="secondary"
      indicatorColor="secondary"
      >
        <Tab value="one" label="" />
        <Tab value="two" label="" />
        <Tab value="three" label="" />
      </Tabs>
      
      {value === 'one'&&<Home/>}
      {value === 'two'&&<Friends/>}
      {value === 'three'&&<Raiting/>}
      <Toolbar/>
      <BottomNav value={PAGE_PROFILE}/>
      </Container>
    </Paper>
  )
}

export default Profile