import { Toolbar, Box, Tab, Tabs, Typography, Button, Stack, Container } from '@mui/material'
import { Panel } from '@vkontakte/vkui'
import React from 'react'
import { useRouter } from '@happysanta/router'
import { PAGE_MAIN } from '../routers'

function Training() {
    const router = useRouter()
    return (
        <Paper sx={{ width: '100vw', height: '100%', minHeight: '100vh', borderRadius:0 }}>
            <Stack
            sx={{ width: '100vw', height: '100vh' }}
            direction={'column'}
            alignItems='center'
            justifyContent='center'
            spacing={1}>
            <Typography>    </Typography>
            <Button onClick={()=>router.pushPage(PAGE_MAIN)}></Button>
            <Toolbar/>
            </Stack>
        </Paper>
    )
}

export default Training