import { BottomNavigation, BottomNavigationAction, Paper } from '@mui/material'
import React from 'react'
import { useRouter } from '@happysanta/router'
import { PAGE_MAIN, PAGE_PROPERTY, PAGE_BANK, PAGE_PROFILE } from '../routers';

import HandshakeIcon from '@mui/icons-material/Handshake';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import HomeIcon from '@mui/icons-material/Home';
import MonetizationOnIcon from '@mui/icons-material/MonetizationOn';

function BottomNav({value}) {
    const router = useRouter()
    const handleChange = (event, newValue) => {
        router.pushPage(newValue)
      };
    return (
        <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
        <BottomNavigation
            value={value}
            onChange={handleChange}
        >
            <BottomNavigationAction label="" value={PAGE_MAIN} icon={<MonetizationOnIcon/>}/>
            <BottomNavigationAction label="" value={PAGE_PROPERTY} icon={<HandshakeIcon/>}/>
            <BottomNavigationAction label="" value={PAGE_BANK} icon={<AccountBalanceIcon/>}/>
            <BottomNavigationAction label='' value={PAGE_PROFILE} icon={<HomeIcon/>}/>

        </BottomNavigation>
        </Paper>
    )
}

export default BottomNav